import React from 'react'
import styled from 'styled-components'

export const Header = () => {
  return (
    <StyledHeader>
      <a href="https://bit.ly/3ZPBeG1" target="_blank">
        My pet projects on GitHub
      </a>
    </StyledHeader>
  )
}

/* ---------------------------------- Style --------------------------------- */
const StyledHeader = styled.header`
  display: flex;
  align-items: center;
  justify-content: space-around;

  width: 100%;
  height: 50px;

  background: #007bff;
  color: white;
`
